.navbar {
    width: 100%;
    box-sizing: border-box;
    height: 88px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 40px;
    border-bottom: 1px solid #ebf0f5;
  }
  .logo {
    height: 40px;
  }
  
  .profile {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 185px;
    height: 44px;
    padding: 9px;
    border-radius: 10px;
    border: 1px solid #ebf0f5;
  }
  .profile > img {
    width: 26px;
    height: 26px;
    border-radius: 50%;
  }
  .profile > p {
    font-weight: 700;
    font-size: 16px;
    line-height: 28px;
    color: #1e272e;
  }
  