.stats {
    box-sizing: border-box;
    border: 1px solid #ebf0f5;
    border-radius: 10px;
    height: 158px;
    width: 717px;
    padding: 30px;
    margin: 30px 0;
  }
  .stats > p {
    margin-bottom: 20px;
    font-weight: 700;
    font-size: 16px;
    line-height: 28px;
    color: #1e272e;
  }
  .achievement {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .item {
    display: flex;
  }
  .img {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background: #f7f8fa;
    border: 1px solid #ebf0f5;
    margin-right: 20px;
  }
  .item > .content > h6 {
    font-weight: 700;
    font-size: 22px;
    line-height: 29px;
    color: #1e272e;
  }
  .item > .content > p {
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    color: #9eaab7;
  }
  